/* MAIN TITLE SECTION */

#mainTitleSection{
   background-image: url('../../Images/HotelExteriorThree.jpg');
   height: 750px; 
   background-size: cover;
}



#MainTitleSectionText {
    position: relative;
    top: 40%; 
    left: 25%; 
    width: 50%; 
    padding: 1%; 
    border-radius: 10px;
    color: white; 
    text-align: center; 
    font-size: 35px; 
    background-color:rgba(0,0,0,.6)
}

/* MAIN SECTION DIVIDE ONE */ 

#mainSectionDivideOne {
height: 750px
}


#mainSubSectionDivideOne {
    position: relative; 
    background-color: white; 
    width: 30%; 
    top: 40%; 
    left: 10%; 
    z-index: 26
}

#mainSubSectionDivideTwo {
   position:relative;
   top:-10%;
   display:flex;
   justify-content: center;
}

#imgSubSectionDivideTwo {
    height: 50%; 
    width: 50%; 
}

/* MAIN SECTION DIVIDE TWO */ 

#mainSectionDivideTwo{
    height: 750px; 
    color: white; 
    background-image: url('../../Images/rockyMountainOne.jpeg');
    background-size: cover;
}

#mainSectionDivideTwoText {
    position: relative;
    top: 40%; 
    left: 25%; 
    width: 50%; 
    padding: 1%; 
    border-radius: 10px;
    color: white; 
    text-align: center; 
    font-size: 20px; 
    background-color:rgba(0,0,0,.6)
}


/* MAIN SECTION ROOM DESCRIPTION */ 

#mainSectionRoomDescription {
    display: grid; 
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr; 
    justify-content: center;
    grid-gap: 5%;
  
}

#mainSectionRoomPicOne{
    position: relative;  
    top: 10%;
    left: 15%; 
    order: 1; 
}

#mainSectionRoomQuote{
    order: 2
}

#mainSectionRoomQuoteText{
  width:95%; 
  padding-top: 15%;  
  font-size: 30px;
  text-align:center; 
}

#mainSectionSummary {
    width: 80%; 
    text-align: center; 
    padding-left: 12%;
    padding-top: 12%; 
    order: 3
}

#mainSectionRoomPicTwo{
    order: 4
}

#mainSectionRoomPicTwoImage{
 position: relative;
 bottom: 15%;
 right: 15%;
}


.mainSectionImgClass {
    width: 100%;
    height: 100%;
}

/* MAIN SECTION GENERAL INFORMATION */

#mainSectionGeneralInfo { 
    text-align: center;
}

#mainSectionGeneralInfo > h1 {
 margin-top: 5%; 
}

#mainSectionGenralInfoContainer{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

#mainSectionGeneralInfoImg{
    width: 60%; 
}

#mainSectionGeneralInfoText {
    display:grid;
    grid-template-rows: 1fr 1fr 1fr;
   
}

#mainSectionGeneralInfoTitleText{
    font-size: 50px; 
}

.mainSectionGeneralInfoSubText{
    border-top: solid grey 1px;
    border-bottom: solid grey 1px; 
    padding-right: 50px; 
    
    font-size: 15px;  
}
.mainSectionGeneralInfoSubContainer{
display: grid; 
justify-items: center;
text-align: center; 
padding-top: 10%; 
}

#generalPhoneIcon, #generalEmailIcon, #generalHotelIcon{ 
    height: 100px;
    width: 50%;
    margin-bottom: 5%; 
}


@media (max-width: 900px){
    /**MAIN TITLE SECTION **/

    #MainTitleSectionText {
   font-size: 25px; 
    }
    /**MAIN SECTION DIVIDE ONE **/
    #mainSectionDivideOne {
        height: 100%
        }
    #mainSectionDivideOne {
        display: grid; 
        justify-content: center;
    }
    #mainSubSectionDivideOne, #mainSubSectionDivideTwo{
        position: static; 
    }
    #mainSubSectionDivideOne{
        width: 100%; 
    }
   
    #imgSubSectionDivideTwo{
        height: 100%;
        width: 100%; 
    }
    /**MAIN SECTION ROOM DESCRIPTION**/
    #mainSectionRoomDescription{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
    }


    #mainSectionRoomPicOne{
        position: static;  
        padding-top: 3%; 
        order: 1; 
    }
    #mainSectionRoomPicTwo{
        order: 3;
    }

    #mainSectionSummary {
        order: 4;
        padding-top: 0%; 
    }
    
    #mainSectionRoomPicTwoImage {
       position: static; 
    }

    #mainSectionRoomQuote{
        order: 2;
    }

    #mainSectionRoomQuoteText{
        position: relative;
        top: 10%;
        width: 100%;  
        font-size: 30px;
        text-align:center; 
      }
    /* MAIN CAROUSEL */ 

    #mainSectionCarousel {
     margin-top: 15%
    }

    /* MAIN SECTION GENERAL INFO */

    #mainSectionGeneralInfo {
        display: grid; 
        grid-template-columns: 1fr

    }
   
    .mainSectionGeneralInfoSubContainer{
        margin-left: 5%;
        margin-right: 5%; 
    }
}